<template>
     <div class="content">
        <van-nav-bar
        title="追评任务"
        left-text="返回"
        right-text=""
        left-arrow
        @click-left="goBack"
        />
        <div class="pay_way_group">
            <van-cell-group class="payment_group">
                <van-cell title="使用买号">{{task.tbUser}}</van-cell>
                <van-cell title="订单编号">{{task.orderNo}}</van-cell>
                <van-cell title="店铺名称">{{goods.shop_name}}</van-cell>
                <van-cell title="件数">{{task.goodsNum}}件</van-cell>
                <van-cell title="返现金额">{{task.tbMoney}}元</van-cell>
                <van-cell title="佣金奖励">{{task.brokerage}}元</van-cell>
                <van-cell title="商品主图"><img width="100%" v-lazy="goods.main_img" @click="showImagePreview(goods.main_img)" /></van-cell>
            </van-cell-group>
        </div>

        <div class="pay_way_group">
            <van-cell-group>
               <van-cell class="tip_r" title="温馨提示：">如果乱评价，将扣除本单佣金</van-cell>
               <van-cell class="tip_r" title="评价要求：">请按照下方指定要求评价~!。</van-cell>
            </van-cell-group>
        </div>

        <div v-if="task.commentType == 4" class="pay_way_group">
            <van-cell-group>
                 <van-cell title="追评文字">
                   {{task.chaseCommentBody}}
                   <div v-if="task.chaseCommentBody!=''" v-clipboard:copy="task.chaseCommentBody" v-clipboard:success="onCopy" v-clipboard:error="onError" class="tip_r">点击复制</div>
                 </van-cell>
                 <van-cell title="追加好评奖励">{{task.ChaseCommentMoney}}元</van-cell>
            </van-cell-group>
            <div class="pic_list">
                <div class="pic_title">追评图片</div>
                <div class="flex">
                    <img  v-for="(com ,i) in task.chaseCommentPic" :key="i" @click="showImagePreview(com)" :src="com" />
                </div>
            </div>
        </div>
        <div class="pay_way_group">
          <van-cell-group>
              <div class="upload-title">商家会在后台核对，无需截图好评</div>
<!--              <div class="upload-desc">好评示列（点击可放大）</div>-->
<!--              <div class="demo-pic">-->
<!--                 <img :src="demoImg" @click="showImagePreview(demoImg)" width="50%">-->
<!--              </div>-->
            </van-cell-group>
        </div>
        <div class="btn_list">
            <van-button v-if="task.chaseCommentStatus!=1 && task.chaseCommentStatus!=2" class="btn" @click="subComment" :loading="isSubmit" type="danger" :bottom-action="true">提交审核</van-button>
            <van-button class="btn" :loading="false" type="default" @click="goBack" :bottom-action="true">返回</van-button>
        </div>

     </div>
</template>

<script>
import {
  Row,
  Col,
  CellGroup,
  Cell,
  Button,
  Dialog,
  ImagePreview,
  Toast,
  Field
} from "vant";

import { SALES, COMMENTS } from "@/api/task";

export default {
  name: "ChaseCom",
  components: {
    [Row.name]: Row,
    [Col.name]: Col,
    [CellGroup.name]: CellGroup,
    [Cell.name]: Cell,
    [Button.name]: Button,
    [Toast.name]: Toast,
    [ImagePreview.name]: ImagePreview
  },
  props: {
    id: this.id
  },
  created() {
    this.initPage();
  },
  data() {
    return {
      isSubmit: false,
      goods: [],
      task: [],
      files:'',
      demoImg: require(`@/assets/images/hp.jpg`)
    };
  },
  methods: {
    initPage() {
      if (!this.id) {
        Toast.fail("参数错误");
        return;
      }
      this.$reqGet(SALES + this.id)
        .then(res => {
          this.getTaskBack(res);
        })
        .catch(err => {
          this.getTaskBack(err);
        });
    },
    //任务订单数据
    getTaskBack(res) {
      let result = res.data;
      if (result.code != 0) {
        let msg = result && result.msg ? result.msg : "获取任务失败";
        Toast.fail(msg);
        return;
      }
      let data = result.data;
      this.task = data.task;
      this.goods = data.goods;
    },
    //提交评论任务
    subComment() {
      let formdata = new FormData();
      formdata.append("order_id", this.id);
      formdata.append("comment_type",2);
      this.isSubmit = true;
      this.$reqPost(COMMENTS, formdata)
        .then(res => {
          this.commentBack(res);
        })
        .catch(err => {
          this.commentBack(err);
        });
    },
    //提交成功回调
    commentBack(res) {
      this.isSubmit = false;
      let result = res.data;
      if (result.code != 0) {
        let msg = result && result.msg ? result.msg : "获取任务失败";
        Toast.fail(msg);
        return;
      }
      let gainMoney = this.task.ChaseCommentMoney;
      let tipStr = `<p style="text-align: center;">商家会再12小时内核对，超时会自动核对通过</p>
      <p style="text-align: center;">核对通过之后，佣金${gainMoney}元即会充值到您的账号中，</p>
      <p style="text-align: center;">届时您可以申请提现~！</p>`;
      Dialog.alert({
        title: "追评任务提交成功",
        message: tipStr
      })
        .then(() => {
          this.$router.push({name: 'chase'});
        })
        .catch(err => {});
    },
    onCopy(e) {
      Toast("复制成功");
    },
    onError() {
      Toast("复制失败");
    },
    //返回
    goBack() {
      this.$router.push({name: 'chase'});
    },
    //图片预览
    showImagePreview(img) {
      ImagePreview([img]);
    },
    setFiles(file){
      console.log(file)
      this.files = file
    },
  }
};
</script>

<style>
.pay_way_title {
  font-weight: bold;
  padding: 15px;
  background-color: #fff;
}
.tip_r {
  color: red;
}
.pic_list {
  padding: 10px 15px;
  background-color: #fff;
}
.flex {
  display: flex;
  display: -webkit-flex;
  flex-wrap: wrap;
  margin-top: 15px;
}
.flex img {
  width: 80px;
  height: 80px;
  margin: 8px 4px 0 0;
}
.pic-box {
  width: 100%;
  overflow-y: scroll;
}
.btn {
  margin-bottom: 15px;
}
.btn_list {
  padding: 20px;
  background-color: #fff;
}
.upload-title {
  color: red;
  padding: 12px;
  font-size: 16px;
}
.upload-desc{
  font-size: 16px;
  font-weight: bold;
  padding: 15px 0;
  color: darkred;
  text-align: center;
}
.demo-pic{
  text-align: center;
}
</style>



